.toggle-button {
  z-index: 999;
  background-color: #0000;
  width: 30px;
  height: 30px;
  display: block;
  position: relative;
}

.toggle-button:after, .toggle-button:before {
  content: "";
}

.toggle-button:before {
  top: 20%;
}

.toggle-button__line {
  top: calc(50% - 1px);
}

.toggle-button:after {
  top: 70%;
}

.toggle-button__line, .toggle-button:before, .toggle-button:after {
  background-color: #000;
  width: 100%;
  height: 2px;
  transition: transform .5s linear, top .5s linear .5s;
  position: absolute;
  left: 0;
}

.toggle-button.open .toggle-button__line {
  transform-origin: 0;
  transform: scaleX(0);
}

.toggle-button.open:before, .toggle-button.open:after {
  top: calc(50% - 1px);
}

.toggle-button.open:before {
  transform: rotate(45deg);
}

.toggle-button.open:after {
  transform: rotate(-45deg);
}

@media (width >= 768px) {
  .toggle-button {
    display: none;
  }
}

